
















































































































import { updateData, fetchConfig } from '@/api/sysManage/ossManage'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { OssConfig } from '@/domains/oss'

@Component({
  name: 'OssEdit'
})
export default class Edit extends Vue {
  @Prop({ default: 'save' })
  private type!: string
  /** form数据 */
  private ruleForm: any = {}
  /** form校验 */
  private rules: any = {}
  private created() {
    if (this.type === 'edit') {
      this.getConfig()
    }
  }
  // 校验提交
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 保存
  private handleSubmit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 修改
  private handleEdit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 取消
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 获取云配置信息
  getConfig() {
    fetchConfig().then((response: any) => {
      const config: OssConfig = response.config
      this.ruleForm = OssConfig.build(config)
    })
  }
}
