/** OSS上传文件的配置信息OssConfig */
export default class OssConfig {
  public static build(data: OssConfig) {
    const ossConfig: OssConfig = new OssConfig()
    ossConfig.type = data.type
    ossConfig.aliyunAccessKeyId = data.aliyunAccessKeyId
    ossConfig.aliyunAccessKeySecret = data.aliyunAccessKeySecret
    ossConfig.aliyunBucketName = data.aliyunBucketName
    ossConfig.aliyunDomain = data.aliyunDomain
    ossConfig.aliyunEndPoint = data.aliyunEndPoint
    ossConfig.aliyunPrefix = data.aliyunPrefix
    ossConfig.minioAccessKey = data.minioAccessKey
    ossConfig.minioBucketName = data.minioBucketName
    ossConfig.minioDomain = data.minioDomain
    ossConfig.minioPrefix = data.minioPrefix
    ossConfig.minioSecretKey = data.minioSecretKey
    ossConfig.qcloudAppId = data.qcloudAppId
    ossConfig.qcloudBucketName = data.qcloudBucketName
    ossConfig.qcloudDomain = data.qcloudDomain
    ossConfig.qcloudPrefix = data.qcloudPrefix
    ossConfig.qcloudRegion = data.qcloudRegion
    ossConfig.qcloudSecretId = data.qcloudSecretId
    ossConfig.qcloudSecretKey = data.qcloudSecretKey
    ossConfig.qiniuAccessKey = data.qiniuAccessKey
    ossConfig.qiniuBucketName = data.qiniuBucketName
    ossConfig.qiniuDomain = data.qiniuDomain
    ossConfig.qiniuPrefix = data.qiniuPrefix
    ossConfig.qiniuSecretKey = data.qiniuSecretKey
    return ossConfig
  }
  /** 存储类型，启用哪个引擎，0拓胜云，1七牛，2阿里云，3腾讯云 */
  public type: number = 0
  /** 阿里云AccessKeyId */
  public aliyunAccessKeyId: string = ''
  /** 阿里云AccessKeySecret */
  public aliyunAccessKeySecret: string = ''
  /** 阿里云BucketName */
  public aliyunBucketName: string = ''
  /** 阿里云绑定的域名 */
  public aliyunDomain: string = ''
  /** 阿里云EndPoint */
  public aliyunEndPoint: string = ''
  /** 阿里云路径前缀 */
  public aliyunPrefix: string = ''
  /** 拓胜云AccessKey */
  public minioAccessKey: string = ''
  /** 拓胜云存储空间名 */
  public minioBucketName: string = ''
  /** 拓胜云绑定的域名 */
  public minioDomain: string = ''
  /** 拓胜云路径前缀 */
  public minioPrefix: string = ''
  /** 拓胜云SecretKey */
  public minioSecretKey: string = ''
  /** 腾讯云AppId */
  public qcloudAppId: number = 0
  /** 腾讯云BucketName */
  public qcloudBucketName: string = ''
  /** 腾讯云绑定的域名 */
  public qcloudDomain: string = ''
  /** 腾讯云路径前缀 */
  public qcloudPrefix: string = ''
  /** Bucket所属地区，如：sh（可选值 ，华南：gz 华北：tj 华东：sh） */
  public qcloudRegion: string = ''
  /** 腾讯云SecretId */
  public qcloudSecretId: string = ''
  /** 腾讯云SecretKey */
  public qcloudSecretKey: string = ''
  /** 七牛AccessKey */
  public qiniuAccessKey: string = ''
  /** 七牛存储空间名 */
  public qiniuBucketName: string = ''
  /** 七牛绑定的域名 */
  public qiniuDomain: string = ''
  /** 七牛路径前缀 */
  public qiniuPrefix: string = ''
  /** 七牛SecretKey */
  public qiniuSecretKey: string = ''

  /** 索引 */
  readonly [id: string]: string | number
}
