





































































import { Component, Vue } from 'vue-property-decorator'
import TableList from '@/components/TableList/index.vue'
import Pagination from '@/components/Pagination/index.vue'
import Edit from './edit.vue'
import { fetchList, deleteData } from '@/api/sysManage/ossManage'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Sort } from '@/domains/sort'
import { Oss } from '@/domains/oss'
import { Pages } from '@/domains/pages'
import { checkSelection } from '@/utils/checkSelection'
import sortable from '@/utils/sortable'
import { getToken } from '@/utils/auth'
import { baseUrl } from '@/utils/config'

class Page extends Pages {
  public list: Oss[] = []
}

@Component({
  name: 'OssManage',
  components: {
    TableList,
    Pagination,
    Edit
  }
})

export default class OssManage extends Vue {
  /** 是否显示table列表 */
  private isShowList: boolean = true
  /** 查询条件 */
  private listQuery: ListQuery = new ListQuery()
  /** 数据列表 */
  private list: Oss[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: 'ID',
      key: 'id',
      width: 100
    },
    {
      label: 'url',
      key: 'url',
      slot: 'url'
    },
    {
      label: '创建时间',
      key: 'createDate',
      width: 180
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前编辑数据的ID */
  private id: string = '0'
  /** 当前选中的数据 */
  private multipleSelection: Oss[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'id'
  /** 上传的地址 */
  private ossUrl: string = ''
  /** tab标题，编辑或者新增 */
  get tabLabel() {
    return this.type === 'edit' ? this.$t('common.edit') : this.$t('common.create')
  }

  created(): void {
    this.ossUrl = baseUrl + '/sys/oss/upload?token=' + getToken()
    this.getList()
  }

  /** 获取数据列表 */
  private getList(): void {
    fetchList(this.listQuery).then((response: any) => {
      const page: Page = response.page
      this.list = page.list
      this.total = page.totalCount

      // this.newList = this.list.map((v: Role) => v.roleId)
      this.newList = this.list.map((v: Oss) => v[this.idKey] as string)

      const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      sortable(el, this.list, this.newList)
    })
  }
  /** 搜索 */
  private handleFilter(): void {
    this.listQuery._search = true
    this.listQuery.page = 1
    this.getList()
  }
  /** 修改配置 */
  private handleEdit(): void {
    this.type = 'edit'
    this.isShowList = false
  }
  /** 删除数据 */
  private handleDelete(): void {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('是否确定删除该数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids: string[] = []
        this.multipleSelection.map(item => {
          ids.push(item[this.idKey] as string)
        })
        deleteData(ids).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.handleFilter()
        })
      }).catch(() => {})
    })
  }
  /** 排序 */
  private handleSortChange(sort: Sort): void {
    if (sort.sidx !== undefined) {
      this.listQuery.sidx = sort.sidx
    }
    this.listQuery.order = sort.order
    this.handleFilter()
  }
  private handleSelectionChange(val: Oss[]): void {
    this.multipleSelection = val
  }
  /** 监听子组件，执行方法 */
  private getMsgFromChild(v: string): void {
    if (v !== 'cancel') {
      this.handleFilter()
    }
    this.isShowList = true
  }
  // 上传成功
  private onSuccess(response: any) {
    if (response.code === 500) {
      this.$alert(response.msg)
    } else {
      this.getList()
    }
  }
}
