import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { OssConfig } from '@/domains/oss'

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/oss/list',
    method: 'get',
    params: query
  })
}

export function fetchConfig() {
  return request({
    url: '/sys/oss/config',
    method: 'get'
  })
}

export function updateData(data: OssConfig) {
  return request({
    url: '/sys/oss/saveConfig',
    method: 'post',
    data
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/sys/oss/delete',
    method: 'delete',
    data
  })
}
